<template>
  <div class="login">
    <input type="text" placeholder="Username" v-model="username" />
    <input
      type="password"
      placeholder="Password"
      v-model="password"
      maxLength="6"
    />
    <button @click="login">Sign in</button>
  </div>
</template>
<script>
import { login } from "api/user";
export default {
  name: "",
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    login() {
      login(this.username, this.password).then(res => {
        console.log(res);
      });
    }
  }
};
</script>
<style lang="scss" scoped>
@import "~assets/scss/mixins";
@media screen and (min-width: 720px) {
  .login {
    @include flex-center(column);
    input {
      width: 60%;
      height: 40px;
      padding: 0 5%;
      border-radius: 5px;
      margin-bottom: 20px;
      background-color: rgba(255, 255, 255, 0.4);
      color: #575757;
      &:nth-child(2) {
        margin-bottom: 5px;
      }
      &::placeholder {
        color: #575757;
      }
    }
    button {
      width: 40%;
      height: 40px;
      margin-top: 20px;
      border-radius: 5px;
      border: none;
      outline: none;
      background-color: rgba(255, 255, 255, 0.4);
      color: #2c3e50;
      font-size: 16px;
      font-weight: bold;
      &:hover {
        background-color: rgba(255, 255, 255, 0.5);
      }
    }
  }
}
@media screen and (max-width: 719px) {
}
</style>
