import axios from "axios";

const regist = (username, password) => {
  return axios({
    method: "get",
    url: "/api/user/regist",
    data: {
      username,
      password
    }
  })
    .then(res => {
      if (res && res.data) {
        return res.data;
      }
      throw new Error("Failed to get the data!");
    })
    .catch(err => {
      // catch error first before send data to front end
      if (err) {
        console.error(err);
      }
    })
    .then(res => {
      return Promise.resolve(res); // res could be undefined or data
    });
};

const login = (username, password) => {
  return axios({
    method: "get",
    url: "/api/user/login",
    data: {
      username,
      password
    }
  })
    .then(res => {
      if (res && res.data) {
        return res.data;
      }
      throw new Error("Failed to get the data");
    })
    .catch(err => {
      if (err) {
        console.error(err);
      }
    })
    .then(res => {
      return Promise.resolve(res);
    });
};

export { regist, login };
